body {
  color: black;
  background: #d2d7d3;
  background: url("/img/img.jpg");
  background-attachment: fixed;
}

.counter {
  background: #2e3131;
  width: 100%;
  z-index: 1;
  color: #d2d7d3;
  position: fixed;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.counter-items {
  display: flex;
  justify-content: space-between;
}

.counter-items_item {
  margin-left: 1rem;
  margin-right: 1rem;
}

.row-container {
  width: 100%;
  margin-top: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.c1,
.c2 {
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-container {
  width: 100%;
  height: 30rem;
}
.hero {
  position: relative;
}
.overlay {
  position: absolute;
  background: black;
  opacity: 0.7;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-right: -5rem;
  top: 0;
  left: 0;
  width: 100%;
  height: 130vh;
}

.form {
  height: auto;
  width: 25rem;
  min-width: 25rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #d2d7d3;
  z-index: 1;
  margin-top: 4rem;
  z-index: 1;
  border-radius: 4px;
}

.form-form {
  width: 80%;
  height: 60%;
  padding: 2rem;
  border-radius: 8px 8px 8px 8px;
  -moz-border-radius: 8px 8px 8px 8px;
  -webkit-border-radius: 8px 8px 8px 8px;
  border: 1px solid #000000;
}

@media only screen and (max-width: 600px) {
  #span-hour {
    display: none;
  }

  h3 {
    font-size: 1rem;
  }
}
